@use "styles/stylesFunctions.scss" as f;
@use "styles/sassVariables" as v;

.hero-section {
  display: flex;
  flex-direction: column;
  .search-overlay {
    margin-top: -150px;
    z-index: 1;
  }
}

.row-gap {
  margin-top: 65px;
  margin-bottom: 51px;
}

.services-container {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  gap: 30px 30px;
}

.most-search {
  text-align: center;
  margin-top: 92px;
  margin-bottom: 65px;
}

.show-case {
  background: #f8f8f8;
  height: auto;

  padding-top: 70px;
  padding-bottom: 77px;

  display: flex;
  flex-wrap: row wrap;
  gap: 0 70px;
  justify-content: center;
  align-items: center;

  .map-text {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;

    margin-top: 156px;
    margin-bottom: 30px;

    p {
      font: var(--unnamed-font-style-normal) normal
        var(--unnamed-font-weight-300) 48px/78px
        var(--unnamed-font-family-ge-ss-two);
      letter-spacing: var(--unnamed-character-spacing-0);
      text-align: center;
      font: normal normal 300 48px/78px GE SS Two;
      letter-spacing: 0px;
      color: #000000;
      opacity: 1;
    }
    span {
      font: var(--unnamed-font-style-normal) normal
        var(--unnamed-font-weight-300) 35px/57px
        var(--unnamed-font-family-ge-ss-two);
      letter-spacing: var(--unnamed-character-spacing-0);
      text-align: center;
      font: normal normal 300 35px/57px GE SS Two;
      letter-spacing: 0px;
      color: #000000;
      opacity: 1;
    }
  }

  .map-image {
    width: 100%;
    img {
      width: 100%;
    }
  }
}
