@use "styles/stylesFunctions.scss" as f;
@use "styles/sassVariables" as v;

.wrapper {
  background-color: #001736;
  box-shadow: 0px 3px 6px #00000029;
  border-radius: 10px;
  width: max(300px, 80vw);
  @include f.lg() {
    width: 60%;
    padding: 50px;
  }
  padding: 15px 10px;

  margin: 0 auto;

  .header {
    font: var(--unnamed-font-style-normal) normal medium 43px/150px
      var(--unnamed-font-family-ge-ss-two);
    letter-spacing: var(--unnamed-character-spacing-0);
    // text-align: right;
    font: normal normal medium 43px/150px GE SS Two;
    letter-spacing: 0px;
    color: #fff;
    font-size: 30px;
    font-weight: bold;

    font-size: clamp(25px, calc(1rem + 1vw), 35px);

    margin-bottom: 52px;
  }
  .search {
    position: relative;
    input,
    select {
      background: #ffffff 0% 0% no-repeat padding-box;
      border: 1px solid #707070;
      border-radius: 5px;
      height: 75px;
      width: 100%;
      padding: 10px;
      // @include f.lg() {
      //   padding-right: 70px;
      // }
    }
    svg {
      position: absolute;
      top: 10px;
      @include f.rtl(left, 10px, initial);
      @include f.rtl(right, initial, 10px);
      transform: scale(0.4);

      @include f.lg() {
        right: 10px;
        top: 10px;
        transform: scale(0.7);
      }
    }
  }

  .select {
    select {
      background: #ffffff 0% 0% no-repeat padding-box;
      border: 1px solid #707070;
      border-radius: 5px;
      height: 75px;
      width: 100%;
      padding: 10px;

      -webkit-appearance: none;
      -moz-appearance: none;
      background-image: url("data:image/svg+xml;utf8,<svg fill='grey' height='42'  width='42' viewBox='0 0 24 24' xmlns='http://www.w3.org/2000/svg'><path d='M7 10l5 5 5-5z'/><path d='M0 0h24v24H0z' fill='none'/></svg>");
      background-repeat: no-repeat;
      background-position-x: 25px;
      @include f.rtl(background-position-x, 10px, 100%);
      background-position-y: 20px;
      border: 1px solid #dfdfdf;
      border-radius: 2px;
      @include f.xs() {
        margin-top: 1rem;
      }
      @include f.md() {
        margin-top: 0;
      }

      padding: 1rem;
      padding-right: 2rem;
    }
  }

  .button {
    background-color: v.$primary;
    border: none;
    color: white;
    text-align: center;
    text-decoration: none;
    display: inline-block;
    font-size: 25px;

    width: fit-content;
    padding: 15px 50px 11px 50px;

    margin-top: 43px;
    transition: v.$transition;
    @include f.lg() {
      padding: 20px 113px 11px 113px;
    }
    &:hover {
      filter: brightness(1.1);
      // background-color: v.$secondary;
    }
  }
}
