@use "styles/stylesFunctions.scss" as f;
@use "styles/sassVariables" as v;

.container {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  .number {
    width: 50px;
    height: 50px;
    border-radius: 50%;
    background-color: #fff;
    border: 1px solid #001736;

    display: flex;
    justify-content: center;
    align-items: center;

    margin-bottom: 42px;
    span {
      font: var(--unnamed-font-style-normal) normal medium 32px/53px
        var(--unnamed-font-family-ge-ss-two);
      letter-spacing: var(--unnamed-character-spacing-0);
      color: var(--unnamed-color-001736);
      text-align: center;
      font: normal normal medium 32px/53px GE SS Two;
      color: #001736;
    }
  }
  .text {
    p {
      font: var(--unnamed-font-style-normal) normal
        var(--unnamed-font-weight-300) 30px/50px
        var(--unnamed-font-family-ge-ss-two);
      letter-spacing: var(--unnamed-character-spacing-0);
      font: normal normal 300 30px/50px GE SS Two;
      color: #000000;

      max-width: 250px;
      text-align: center;
    }
  }
  .svg {
    width: 100%;
    svg {
      width: 100%;
    }
  }
}
