@use "styles/stylesFunctions.scss" as f;
@use "styles/sassVariables" as v;

.card {
  position: relative;
  overflow: hidden;
  width: 300px;
  height: 235px;
  border-radius: 10px;
  border: 0.5px solid #707070;

  transition: v.$transition;

  img {
    display: block;
    width: 100%;
    height: auto;
    object-fit: cover;

    transition: v.$transition;
  }
  .overlay {
    position: absolute;
    bottom: 0;
    left: 0;
    right: 0;
    height: 20%;
    width: 100%;
    opacity: 1;
    transition: 0.5s ease;
    background-color: #fff;
    padding: 7px 26.4px 7px 26px;
    .text {
      display: flex;
      justify-content: space-between;
      align-items: center;

      h4 {
        font-size: 16px;
        margin: 0;
      }

      svg {
        font-size: 5px;
        // transform: scale(0.9);
      }
    }
  }

  &:hover {
    img {
      transform: scale(1.2);
    }
    .overlay {
      opacity: 1;
    }
  }
}
